/* Typo Grid */
html, .root {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 0;
}
body, .article {
  font-family: 'Arimo', sans-serif;
  -webkit-font-smoothing: antialiased !important;
  font-size: 1em;
  line-height: 1.5em;
  color: #333;
}
h1, h2, h3, h4, h5 {
  font-weight: 700;
}
h1, .h1 {
  font-size: 3.35714286em;
  line-height: 1.34042553em;
  margin-top: 0.44680851em;
  margin-bottom: 0.89361702em;
  letter-spacing: -0.005em;
}
h2, .h2 {
  font-size: 2.28571429em;
  line-height: 1.3125em;
  margin-top: 0.65625em;
  margin-bottom: 0.65625em;
}
h3, .h3 {
  font-size: 1.5em;
  line-height: 1em;
  margin-top: 1em;
  margin-bottom: 0.5em;
}
h4, .h4 {
  font-size: 1.25em;
  line-height: 1.2em;
  margin-top: 1.2em;
  margin-bottom: 0.6em;
}
h5, .h5 {
  font-size: 1em;
  line-height: 1.5em;
  margin-top: 1.5em;
  margin-bottom: 0em;
}
p, ul, ol, pre, table, blockquote {
  margin-top: 0em;
  margin-bottom: 1.5em;
}
ul ul, ol ol, ul ol, ol ul {
  margin-top: 0em;
  margin-bottom: 0em;
}

/* Let's make sure all's aligned */
hr, .hr {
  border: 1px solid;
  margin: -1px 0;
}
a, b, i, strong, em, small, code {
  line-height: 0;
}
sub, sup {
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}


/* Responsive text for mobile */
@media (max-width: 767px) {
    body{font-size: 12px;}
    #banner h2 {
      margin: 0.328125em 0;
      letter-spacing: 0;
    }
}


