/* Dropdown on hover, for tablets, PC */
@media (min-width: 768px) {
  .dropdown .dropdown-menu {
    display: block;
    visibility: hidden;
    opacity: 0;
    -moz-transition: visibility 0s linear 0.1s, opacity 0.1s linear;
    -o-transition: visibility 0s linear 0.1s, opacity 0.1s linear;
    -webkit-transition: visibility 0s linear, opacity 0.1s linear;
    -webkit-transition-delay: 0.1s, 0s;
    -webkit-transition: visibility 0s linear 0.1s, opacity 0.1s linear;
    transition: visibility 0s linear 0.1s, opacity 0.1s linear;
  }
  .dropdown:hover .dropdown-menu {
    visibility: visible;
    opacity: 1;
    -o-transition-delay: 0s;
    -webkit-transition-delay: 0s;
    transition-delay: 0s;
    -o-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
  }
}

.dropdown-menu {
  padding: 0;
  border: none;
}
.navbar .dropdown-menu li a {
  font-size: 15px;
  padding: 10px 30px;
  background: #89AC38;
  color: #fff;
}
.navbar .dropdown-menu a:hover, .dropdown-menu>.active>a:hover {
  background: #6A9800;
  color: #fff;
}
.dropdown-menu>a, .dropdown-menu>a:focus,
.dropdown-menu>.active>a, .dropdown-menu>.active>a:focus {
  background: #89AC38;
  color: #fff;
  border: none;
}
#header .navbar-nav .dropdown>a, #header .navbar-nav>.open>a {
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}

