  font-size: 1.2em;
  line-height: 1.2em;
}
.news-content {
  width: 98%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.news-content h4 {
  margin-top: 0.25em;
  margin-bottom: 0.125em;
  letter-spacing: -0.0125em;
}
.news-content p {
  margin-bottom: 0;
}
.news-content .glyphicon {
  margin-left: 0.25em;
  margin-right: 0.25em;
}
.content-wrapper a {
  color: #fff;
}
#slide-container {
  margin-top: 80px;
}

.schedule-item a {
  color: #6A9800;
}

.schedule-item {
  padding: 1.5em 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#FFFEFF), to(#E7FFFE));
  background-image: -webkit-linear-gradient(top, #FFFEFF 0%, #E7FFFE 100%);
  background-image: -o-linear-gradient(top, #FFFEFF 0%, #E7FFFE 100%);
  background-image: linear-gradient(180deg, #FFFEFF 0%, #E7FFFE 100%);
  -webkit-transition: background-image 300ms ease-in-out;
  -o-transition: background-image 300ms ease-in-out;
  transition: background-image 300ms ease-in-out;
  border-radius: 30px 0 30px 0;
}
.schedule-item:hover {
  background-image: -webkit-linear-gradient(315deg, #E3FDF5 0%, #FFE6FA 100%);
  background-image: -o-linear-gradient(315deg, #E3FDF5 0%, #FFE6FA 100%);
  background-image: linear-gradient(-225deg, #E3FDF5 0%, #FFE6FA 100%);
}
.blue-bg {
  padding: 1.5em 0;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#FFFEFF), to(#E7FFFE));
  background-image: -webkit-linear-gradient(bottom, #FFFEFF 0%, #E7FFFE 100%);
  background-image: -o-linear-gradient(bottom, #FFFEFF 0%, #E7FFFE 100%);
  background-image: linear-gradient(0deg, #FFFEFF 0%, #E7FFFE 100%);
}
.schedule-item h3 a div {
  display: block;
}

.more-link {
  margin-left: 1em;
}


/* Style For Wordpress contents */
.centered {
  margin: 0 auto;
}
img.alignnone {
  padding: 6px;
  margin: 0;
  float: left;
  max-width: 33%;
  width: auto;
  height: auto;
}
img.alignleft {
  float: left;
  padding: 0;
  margin: 0 2em 0 0;
}
img.alignright {
  float: left;
  padding: 0;
  margin: 0 0 2em 0;
}
img.aligncenter {
  display: block;
  padding: 0;
  margin: 0.75em auto;
}
.content p {
  clear: both;
}

/* Star icon after sticky posts */
.sticky:after {
  font-family: 'Glyphicons Halflings';
  content: "\e006";
  display: inline-block;
  margin-left: 6px;
  -webkit-animation: glow 1.5s;
          animation: glow 1.5s;
  color: #AFEA28;
}
@-webkit-keyframes glow {
  0%{
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.5) translate(15px, 0px) rotate(30deg);
            transform: scale(1.5) translate(15px, 0px) rotate(30deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1) translate(-5px, 0px) rotate(-30deg);
            transform: scale(1) translate(-5px, 0px) rotate(-30deg);
    opacity: 0;
  }
}
@keyframes glow {
  0%{
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.5) translate(15px, 0px) rotate(30deg);
            transform: scale(1.5) translate(15px, 0px) rotate(30deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1) translate(-5px, 0px) rotate(-30deg);
            transform: scale(1) translate(-5px, 0px) rotate(-30deg);
    opacity: 0;
  }
}

.featured {
  position: relative;
  width: 15px;
  height: 15px;
  display: inline-block;
  color: pink;
}
.featured:before, .featured:after {
  position: absolute;
  top: -60%;
  font-family: 'Glyphicons Halflings';
  content: "\e006";
  font-size: 50px;
  display: block;
  -webkit-animation: pulse 2s linear infinite;
          animation: pulse 2s linear infinite;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  opacity: 0;
}

.featured:after {
  -webkit-animation: pulse 1.25s linear 1.75s infinite;
          animation: pulse 1.25s linear 1.75s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    opacity: 0;
  }
}

/* For mobile */
@media (max-width: 767px) {
  #header {
    height: 50px;
  }
  #logo {
    width: 45px;
    height: 45px;
  }
  #header > .row {
    padding-top: 50px;
  }
  #sticky-banner {
    height: 60px;
    top: 50px;
    font-size: 10px;
  }
  .news-content p {
    font-size: 14px;
  }
  #slide-container, .membership-header {
    margin-top: 60px;
  }
  .collapse-content {
    display: none;
  }
  #header .navbar-nav > li > a:hover,
  #header .navbar-nav > li > a:active,
  #header .navbar-nav > li.active > a {
    border-bottom: none;
  }
  .navbar-nav > li > a {
    padding-top: 15px;
  }
  /* no margin top when collapsed */
  .navbar-nav {
    margin-top: 0;
  }
  .navbar .dropdown-menu li a {
    font-size: 15px;
    padding: 10px 30px;
    background: #89AC38;
    color: #fff;
  }
  .navbar .dropdown-menu a:hover, .dropdown-menu>.active>a:hover, #header .navbar .navbar-nav .open .dropdown-menu a:hover {
    background: #6A9800;
    color: #fff;
  }
  .dropdown-menu>.active>a, .dropdown-menu>.active>a:focus, .navbar-default .navbar-nav .open .dropdown-menu>li>a,
  .navbar .navbar-nav .open .dropdown-menu>.active>a, .navbar .navbar-nav .open .dropdown-menu>.active>a:focus {
    background: #89AC38;
    color: #fff;
    border: none;
  }
  #header .navbar-nav .dropdown>a, #header .navbar-nav>.open>a {
    background: none;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
}

/* Hide tagline for small screen */
@media (max-width: 888px) {
  #tagline {
    display: none;
  }
}


